import { Button } from 'antd';
import React from 'react';
import ad1 from '../../img/main_ad1.png';
import logo from '../../img/_logo_ver2.1.png';
import { Link } from 'react-router-dom';

const navBtnStyle = {
  // margin: '0 20px',
  border: '1px none',
  color: '#fff',
  textDecorationLine: 'none',
  width: '120px',
  height: '40px',
  fontWeight: 'bold',
  borderRadius: '10px',
  backgroundColor: '#1677ff'
};

const Navbar = () => {
  return (
    <div style={{ width: '100%', padding: '10px 0' }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', maxWidth: '1400px', margin: '0 auto' }}>

        {/* nav   */}
        <a href="/parentingDictionary">
          <img
            style={{
              width: '190px',
              height: '95px'
            }}
            src={logo}
            alt=' '
          />
        </a>

        {/* nav   */}
        <div style={{ display: 'flex', margin: '0 auto' }}>
          <div style={{ margin: '0 20px' }}>
            <Link to='/notice' style={{ margin: '0 30px', width: '120px', display: 'inline-block' }}>
              <Button type='primary' style={navBtnStyle}></Button>
            </Link>
          </div>
          <div style={{ margin: '0 20px' }}>
            <Link to='/' style={{ margin: '0 30px', width: '120px', display: 'inline-block' }}>
              <Button type='primary' style={navBtnStyle}></Button>
            </Link>
          </div>
          <div style={{ margin: '0 20px' }}>
            <Link to='/' style={{ margin: '0 30px', width: '120px', display: 'inline-block' }}>
              <Button type='primary' style={navBtnStyle}></Button>
            </Link>
          </div>
          <div style={{ margin: '0 20px' }}>
            <Link to='/' style={{ margin: '0 30px', width: '120px', display: 'inline-block' }}>
              <Button type='primary' style={navBtnStyle}></Button>
            </Link>
          </div>
        </div>

        {/* nav   */}
        <div >
          <a href='/'>
            <img
              style={{
                objectFit: 'cover',
                width: '190px',
                height: '100px'
              }}
              src={ad1}
              alt=' '
            />
          </a>
        </div>
      </div>
    </div>
  )
}

export default Navbar;